import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
let textInput = `
<div class="form-group row">
    <div class="col-xs-4">
        <div class="input-group">
             <span id="check" class="input-group-addon"><span class="fa fa-tumblr"></span></span>
            <input type="text" class="form-control" id="id" value="ID" readonly="true" >
        </div>
    </div>
    <div class="col-xs-8">
      <div class="row">
        <div class="col-xs-10">
          <textarea id="value" type="textarea" rows="2" class="form-control">$VALUE</textarea>
        </div>
        <div class="col-xs-2">
          <button id="description_btn" class="btn btn-default" title="$description"><span class="fa fa-question"></span></button>
        </div>
      </div>
    </div>
</div>
`;
class DevComment extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(textInput);
        item.prop('id', this.dataObject.id);
        //if (this.dataObject.description == undefined) { item.remove($(item.description_btn)); }
        item.find("#value").prop('readonly', (this.dataObject.editable != undefined) ? !this.dataObject.editable : false);
        item[0].addEventListener("change", (event) => { this.controller.onChangeData(event); });
        item.html(item[0].innerHTML.replace("ID", this.dataObject.id).replace("$VALUE", this.dataObject.value || "").replace("$description", this.dataObject.description || ""));
        this.item = item;
    }
}
export default DevComment;
